"use client"

import { useState } from "react"
import { TextField, Button, Paper, Typography, Box } from "@mui/material"
import { useTheme } from "@mui/material/styles"

export default function ContactForm() {
    const [formData, setFormData] = useState({
        name: "",
        email: "",
        subject: "",
        message: "",
    })
    const [submissionStatus, setSubmissionStatus] = useState(null) // 'success' or 'error'
    const [isSubmitting, setIsSubmitting] = useState(false)
    const theme = useTheme() // Access the custom theme

    const handleChange = (e) => {
        const { id, value } = e.target
        setFormData((prevData) => ({
            ...prevData,
            [id]: value,
        }))
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setIsSubmitting(true)
        setSubmissionStatus(null)

        // Simulate API call
        try {
            await new Promise((resolve) => setTimeout(resolve, 1500)) // Simulate network delay
            console.log("Form submitted:", formData)
            setSubmissionStatus("success")
            setFormData({
                name: "",
                email: "",
                subject: "",
                message: "",
            })
        } catch (error) {
            console.error("Submission error:", error)
            setSubmissionStatus("error")
        } finally {
            setIsSubmitting(false)
            setTimeout(() => setSubmissionStatus(null), 3000) // Clear status after 3 seconds
        }
    }

    return (
        <Paper
            elevation={0} // Use custom shadow from theme
            sx={{
                width: "100%",
                maxWidth: 500,
                padding: 4,
                backgroundColor: "background.paper",
                boxSizing: "border-box",
            }}
        >
            <Box sx={{ textAlign: "center", marginBottom: 4 }}>
                <Typography variant="h4" component="h1" sx={{ marginBottom: 1 }}>
                    <Box component="span" sx={{ color: theme.palette.googleBlue.main }}>
                        C
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleRed.main }}>
                        o
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleYellow.main }}>
                        n
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleGreen.main }}>
                        t
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleBlue.main }}>
                        a
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleRed.main }}>
                        c
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleYellow.main }}>
                        t
                    </Box>{" "}
                    <Box component="span" sx={{ color: theme.palette.googleGreen.main }}>
                        U
                    </Box>
                    <Box component="span" sx={{ color: theme.palette.googleBlue.main }}>
                        s
                    </Box>
                </Typography>
                <Typography variant="subtitle1" sx={{ color: "text.secondary" }}>
                    We'd love to hear from you!
                </Typography>
            </Box>
            <Box component="form" onSubmit={handleSubmit} sx={{ "& > :not(style)": { marginBottom: 3 } }}>
                <TextField
                    id="name"
                    label="Name"
                    variant="outlined"
                    fullWidth
                    value={formData.name}
                    onChange={handleChange}
                    required
                />
                <TextField
                    id="email"
                    label="Email"
                    type="email"
                    variant="outlined"
                    fullWidth
                    value={formData.email}
                    onChange={handleChange}
                    required
                />
                <TextField
                    id="subject"
                    label="Subject"
                    variant="outlined"
                    fullWidth
                    value={formData.subject}
                    onChange={handleChange}
                    required
                />
                <TextField
                    id="message"
                    label="Message"
                    variant="outlined"
                    fullWidth
                    multiline
                    rows={5}
                    value={formData.message}
                    onChange={handleChange}
                    required
                />
                <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    fullWidth
                    disabled={isSubmitting}
                    sx={{ marginTop: 2 }}
                >
                    {isSubmitting ? "Sending..." : "Send Message"}
                </Button>
                {submissionStatus === "success" && (
                    <Typography sx={{ textAlign: "center", color: "#34c759", marginTop: 2 }}>
                        Your message has been sent successfully!
                    </Typography>
                )}
                {submissionStatus === "error" && (
                    <Typography sx={{ textAlign: "center", color: "#ff3b30", marginTop: 2 }}>
                        There was an error sending your message. Please try again.
                    </Typography>
                )}
            </Box>
        </Paper>
    )
}
